.title{
    margin-top: 5vh;
    color:#009182;
}

.container{
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #0c0c0c;
}

.schoolContainer{
    display: flex;
    background-color: #0c0c0c;
}

.introContainer{
    margin-top: 2vh;
    margin-left: 8vw;
    margin-right:8vw;
    width: 30vw;
    height:20vw;
    display: flex;
    flex-direction: column;
    padding: 2vw;
    justify-content: center;
    color:rgb(204, 203, 203);
    border-radius: 12px;
    font-size: larger;
}

.introContainer:nth-child(2){
    text-align: right;
}

.introContainer span{
    color:#0c0c0c;
}

.introContainer span:hover{
    color:#009182;
    cursor: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'  width='40' height='48' viewport='0 0 100 100' style='fill:black;font-size:24px;'><text y='50%'>ðŸ¤£</text></svg>")
        16 0,
    auto;
}

.image{
    align-self: flex-end;
    border-radius:20vw;
    padding-left:20px;
    padding-right:20px;
    padding-top:20px;
    padding-bottom:20px;
    width: 15vw;
    height:15vw;
    margin-right:8vw;
    margin-left: 8vw;
}