.container{
    width:100vw;
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
}

.title{
    margin-top: 5vh;
    color:#009182;
}

.categories{
    margin-top:10vh;
    display:flex;
    justify-content: space-evenly;
    width:100vw;
    min-height:50vh;
}

a{
    text-decoration: none;
    color:#009182;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 10px;
    text-align: center;
    transition:0.6s
}

a:hover{
    background-color: #474747;
    text-decoration: none;
    color:#009182;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 10px;
    text-align: center;
    transition:0.6s
}

.cardName{
    font-size:1.5vw;
    margin-top:.5vh;
    margin-bottom: 1vh;
    font-weight:bold;
}

.card{
    height:42vh;
    width:35vw;
}

.card:hover{
    height:45vh;
    width:38vw;
}

.cardImage{
    width:100%;
}

.note{
    font-weight:bold;
    text-align: left;
}

.mobileCard{
    height:28vw;
    width:11.25vw;
    margin-bottom:10vh;
}

.mobileCard:hover{
    padding-top:2vh;
    height:30vw;
    width:12.25vw;
}

.mobileCardImage{
    max-width:100%;
    height:25vw;
}

